import classes from "./AboutPage.module.css"

function AboutPage() {

    return (
       <div className={classes.bigContainer}>
          <section className={classes.smallContainer}>
              <h1>Meie eesmrk</h1>
              <p>
                 Vekivid on esimene Euroopa kivinduse superrakendus. Vitleme paremate kivide eest ja
                 vlismaa kivide vastu, pakkudes paremat alternatiivi igale otstarbele, mida kivi
                 teenindab - sealhulgas kiviteenus, hiskasutatavad kivid ja tukekivi ning toidu
                 ja toidukaupade kohaletoomine...
              </p>
          </section>
          <section className={classes.smallContainer}>
             <h1>100% kodumaine</h1>
             <p>
                Usume, et enamik kivisid ei nua isiklikku kivi kasutamist.
                Vekivis ehitame tulevikku, kus inimesed on sunnitud liikumiseks kivi ostma.
                Seal, kus inimestel on vabadus soovi korral kivi kasutada, valides igaks elujuhtumiks
                sobivaima kivi.
             </p>
          </section>
       </div>

    )
}
export default AboutPage;